.notifications-item {
  max-width: 75rem;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-flow: row wrap;
  border-bottom: 1px solid #e6e6e6;
  padding: 15px 0 44px;
  position: relative; }
  .notifications-item:first-child {
    border-top: 1px solid #e6e6e6; }
  .notifications-item__title {
    font-size: 1.3125em;
    line-height: 1.2;
    margin: 0;
    text-align: left;
    margin-bottom: 11px; }
  .notifications-item__date {
    color: #b3b3b3;
    margin-bottom: 10px;
    display: inline-block; }
  .notifications-item__picture {
    flex: 0 0 auto;
    width: 55px;
    padding-top: 6px; }
  .notifications-item__content {
    flex: 1 1 0px;
    min-width: initial;
    padding-left: 15px; }
  .notifications-item__body, .notifications-item__preview.hide {
    transition: max-height 0.3s linear, opacity 0.5s ease; }
  .notifications-item__preview ~ .notifications-item__body {
    max-height: 0;
    margin: 0;
    padding: 0;
    opacity: 0;
    overflow: hidden; }
  .notifications-item__preview.hide ~ .notifications-item__body {
    max-height: 10000px;
    opacity: 1; }
  .notifications-item__toggler {
    width: 97px;
    height: 20px;
    display: none;
    text-align: center;
    position: absolute;
    left: calc(50% - 25px);
    bottom: 10px;
    border: 1px solid #E6E6E6;
    border-bottom: 0;
    border-top-left-radius: 3px;
    border-top-right-radius: 3px; }
    .notifications-item__toggler i {
      font-size: 1.5em;
      color: #B3B3B3; }
  .notifications-item__preview ~ .notifications-item__toggler {
    display: block; }
  .notifications-item__preview.hide ~ .notifications-item__toggler i::before {
    content: "\f0d8";
    font-family: "FontAwesome"; }
